.content__main {
	.container {
		width: 100%;
	}
}

.buy {
	display: flex;
	margin-bottom: 30px;
	input,
	.button {
		font-size: 20px;
		padding: 10px 16px;
		@media (max-width: $bp2) {
			font-size: 16px;
		}
	}
	input {
		padding: 10px;
	}
}
.price_product {
	display: flex;
	flex-direction: column;
	margin-right: 30px;
	.price__item_new {
		.price__value {
			font-size: 24px;
			@media (max-width: $bp2) {
				font-size: 20px;
			}
		}
	}
	.price__item_old {
		margin-top: 10px;
		.price__value {
			font-size: 16px;
		}
	}
}
// to device file?
.price__item {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
}

.device-full {
	margin-bottom: 60px;
}

.device-basic {
	display: flex;
	margin-bottom: 60px;
	@media(max-width: $bp4) {
		flex-direction: column;
	}
}
.gallery {
	overflow: hidden;
	@media(max-width: $bp4) {
		margin-bottom: $gap-xl;
	}
}
.device-basic__tags {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	margin-bottom: 20px;
	.tag {
		margin: 0 10px 10px 0;
	}
}
.device-basic__column {
	&:first-child {
		flex: 0 0 40%;
		margin-right: 100px;
		position: relative;
		.tags {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
		}
		@media(max-width: $bp4) {
			flex-basis: 100%;
			margin-right: 0;
		}
	}
	&:last-child {
		flex: 1 1 auto;
	}

}
.price_product {
	font-size: 24px;
	display: flex;
	margin-bottom: $gap-md;
	.price__item {
		margin-right: $gap-md;
		&:last-child {
			margin-right: 0;
		}
	}
	.price__item_old {
		.price__value {
			font-weight: 400;
		}
	}
	.price__label {
		font-size: 12px;
		color: $gray-2;
		margin-bottom: $gap-xs;
	}
	.price__value {
		font-size: 24px;
		font-weight: 700;
	}
}
.order_product {
	margin-right: $gap-md;
}
.product-actions {
	display: flex;
	margin-bottom: 30px;
}
.add_product {
	display: flex;
	> *:first-child {
		margin-right: 15px;
	}
}
.info-link {
	margin-left: 30px;
	display: flex;
	align-items: center;
	img {
		margin-right: 5px;
	}
}
.product-cards {
	&__heading {
		font-size: 16px;
		font-weight: 700;
		margin-bottom: 10px;
	}
	&__wrap {
		display: flex;
	}
	&__item {
		margin-right: 20px;
		flex: 0 0 80px;
		img {
			width: 80px;
			height: 50px;
			object-fit: cover;
			display: block;
			border-radius: 4px;
		}
	}
	&__title {
		font-size: 14px;
		line-height: 1;
		margin-top: 5px;
	}
}


.tabs_product {}
.tabs__nav {
	display: flex;
	border-bottom: 1px solid $gray-2;
	margin-bottom: $gap-md;
}
.tab {
	display: flex;
	padding: 10px 16px;
	margin-bottom: -1px;
	font-size: 16px;
	margin-right: 10px;
	@media (max-width: $bp2) {
		font-size: 18px;
	}
}
.tab_active {
	background-color: #fff;
	border: 1px solid $gray-2;
	border-bottom-color: #fff;
	color: $tc1;
	font-weight: 700;
	border-radius: 4px 4px 0 0;
}
.tab-container {
	p {
		line-height: 1.5;
	}
}

.full-params {
	td {
		font-size: 16px;
		color: #828282;
		font-weight: 400;
		position: relative;
		padding-left: 0;
		@media (max-width: $bp2) {
			font-size: 16px;
		}
		span {
			background-color: #fff;
			padding: 0 3px 0 0;
			z-index: 2;
			position: relative;
		}
		&[colspan="2"] {
			color: $tc1;
			padding-top: 20px;
			&:before {
				display: none !important;
			}
		}
		&:first-child {
			&:before {
				content: '';
				display: block;
				position: absolute;
				bottom: 12px;
				left: 0;
				width: 100%;
				height: 1px;
				background-color: #e0e0e0;
			}
		}
	}
	tr {
		border: none;
	}
}


hr {
	border-bottom: 1px solid $gray-1;
	margin-bottom: $gap-lg;
}


.extra-info {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-md;
	@media(max-width: $bp4) {
		grid-template-columns: 1fr;
	}
}
.extra-info__title {
	margin-bottom: $gap-md;
	font-weight: 700;
	font-size: 20px;
	span {
		margin-left: 4px;
	}
}
.extra-info__list {
	padding-left: 16px;
}
.extra-info__list-item {
	font-size: 13px;
	margin-bottom: $gap-md;
	position: relative;
	line-height: 1.3;
	&:last-child {
		margin-bottom: 0;
	}
	&:before {
		content:"";
		display: block;
		width: 6px;
		height: 6px;
		background-color: $secondary-color;
		border-radius: 50%;
		position: absolute;
		top: 3px;
		left: -16px;
	}
}
table.params {
	display: block;
	tr {
		display: flex;
		position: relative;
		border: none;
		&:before {
			content:"";
			display: block;
			width: 6px;
			height: 6px;
			background-color: $secondary-color;
			border-radius: 50%;
			position: absolute;
			top: 8px;
			left: 0;
		}
		td {
			padding: 4px 8px;
			&:first-child {
				color: $gray-2;
				padding-left: 16px;
				padding-right: 4px;
				&:after {
					content: ":";
				}
			}
			&:last-child {
				font-weight: normal;
			}
		}
	}
}
table.full-params {
	th {
		text-align-last: left;
		padding-top: 16px;
		padding-bottom: 8px;
	}
}
.device__info {
	table.params {
		tr {
			&:before {
				display: none;
			}
		}
		td {
			&:first-child {
				padding-left: 0;
			}
		}
	}
}
