input {
	&[type=number] {
		width: 50px;
	}
}

input[type=checkbox] {
	margin-right: $gap-xs;
}

.input, select {
	font-size: 16px;
	line-height: 1;
	border: 1px solid $border-color;
	padding: 6px 10px;
	border-radius: 2px;
}
.button {
	white-space: nowrap;
	line-height: 1;
	font-size: 16px;
	padding: 6px 10px;
	background-color: $primary-color;
	color: #fff;
	border-radius: 4px;
}


.button_checkout {
	color: #fff;
	background-color: $secondary-color;
	border-color: $secondary-color;
}
.button:hover {
	background-color: darken($primary-color, 10%);
	border-color: darken($primary-color, 10%);
}
.button_2 {
	color: $button-2-color;
	background-color: $button-2-background;
	border: 1px solid $button-2-border-color;
}
.button_2:hover {
	background-color: darken($secondary-color, 3%);
	border-color: darken($secondary-color, 3%);
}
.button_checkout:hover {
	background-color: darken($secondary-color, 10%);
	border-color: darken($secondary-color, 10%);
}
.button_request {
	background-color: #fff;
}

.form__item {
	display: flex;
	flex-direction: column;
	margin-bottom: $gap-md;
	&:last-child {
		margin-bottom: 0;
	}
}
.form__element {
	font-size: 16px;
	line-height: 1;
	border: 1px solid $gray-2;
	padding: 6px 10px;
}
.form-label {
	font-size: 14px;
	margin-bottom: $gap-xs;
	font-weight: 700;
	a {
		margin-left: 10px;
		font-weight: 400;
	}
}
.form__text {
	margin-bottom: $gap-lg;
}
textarea {
	min-height: 100px;
}
