.wrapper {
	transition: transform 300ms ease-out;
	&.visible-no {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		transform: translate(-80%, 0);
	}
}
.menu-container {
	// display: none;
	visibility: hidden;
	opacity: 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	visibility: hidden;
	overflow: hidden;
	transition: transform 300ms ease-out;
	&.visible-yes {
		visibility: visible;
		// display: block;
		opacity: 1;
		.menu-content {
			transition: transform 300ms ease-out;
			transform: translate(-80%, 0);
		}
	}
	.menu-content {
		width: 100%;
		height: 100%;
		margin-left: 100%;
		background-color: #fff;
		font-size: 16px; //var
		overflow-y: scroll;
		padding-right: 20%;
	}
	.menu-overlay {
		position: absolute;
		top: 0;
		right: 80%;
		left: 0;
		height: 100%;
		background-color: $overlay-color;
	}
	ul {
		padding: 16px;
		list-style: none;
		border-bottom: 1px solid $separator-color;
		display: block;
		li {
			margin-bottom: $gap-md;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}


.nav-container {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #fff;
	.content {
		height: 100%;
		overflow-y: scroll;
		padding: unset;
	}
	.small-nav {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid $border-color;
		> * {
			padding: $gap-md $gap-sm;
		}
		.header {
			flex: 1;
			text-align: center;
			font-weight: 700;
			background-color: #fff;
		}
	}
	li {
		padding: 10px 15px;
		border-bottom: 1px solid $border-color;
		display: flex;
		a {
			flex: 1;
			line-height: 1.4;
		}
	}
	.next {
		position: absolute;
		left: 100%;
		top: 0;
		background-color: #fff;
		width: 100%;
	}
	.close {
		font-size: 24px;
		font-weight: 700;
		color: gray; // var
		padding: $gap-md;
	}
}
