.devices__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 80px 40px;
	@media (max-width: $bp2) {
		grid-template-columns: repeat(3, 1fr);
	}
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp5) {
		grid-template-columns: 1fr;
	}
}
.devices__wrap_rows {
	grid-template-columns: 1fr;
	grid-row-gap: 10px;
}
