.ads {
	margin-bottom: 80px;
	@media (max-width: $bp4) {
		margin-bottom: 30px;
	}
	.container {
		@media (max-width: $bp4) {
			padding: 0;
		}
	}
}
.ads__wrap {
	display: flex;
	@media (max-width: $bp3) {
		flex-direction: column;
	}

}
.ads__slider {
	height: 340px;
	flex: 1;
	min-width: 0;
	@media (max-width: $bp3) {
		height: 320px;
	}
	@media (max-width: $bp4) {
		height: 180px;
	}
}
.ads__banners {
	flex: 0 0 380px;
	margin-left: 20px;
	display: flex;
	flex-direction: column;
	@media (max-width: $bp3) {
		flex: 0 0 160px;
		flex-direction: row;
		margin-left: unset;
		margin-top: 20px;
	}
	@media (max-width: $bp4) {
		display: none;
	}
}
.ads__banner {
	background-color: #f2f2f2;
	flex: 1;
	&:last-child {
		margin-top: 20px;
		@media (max-width: $bp3) {
			margin-left: 20px;
			margin-top: 0;
		}
	}
}
.slider {
	width: 100%;
	height: 100%;
	display: flex;
	min-width: 0;
	&__item {
		height: 100%;
		display: flex;
		position: relative;
		min-width: 0;
	}
	&__image {
		position: relative;
		flex: 1 0 100%;
		width: 100%;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex: 1 0 100%;
		width: 100%;
		padding: 0 50% 0 70px;
		transform: translateX(-100%);
		@media (max-width: $bp4) {
			padding-left: 30px;
		}
		@media (max-width: $bp5) {
			padding-left: 20px;
		}

	}
	&__title {
		font-size: 40px;
		line-height: 1.2;
		font-weight: 700;
		@media (max-width: $bp4) {
			font-size: 24px;
		}
		@media (max-width: $bp6) {
			font-size: 20px;
		}
	}
	&__subtitle {
		font-size: 24px;
		color: #828282;
		font-weight: 700;
		margin-top: 10px;
		@media (max-width: $bp4) {
			font-size: 16px;
		}
	}
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.slick-track {
	display: flex;
	height: 100%;
}
.slick-list {
	overflow: hidden;
}

.banner {
	width: 100%;
	height: 100%;
	display: flex;
	position: relative;
	> div {
		flex: 1 0 100%
	}
	&__image {
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__content {
		transform: translateX(-100%);
		padding: 0 50% 0 35px;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	&__title {
		font-size: 20px;
		line-height: 1.3;
		font-weight: 700;
	}
	&__subtitle {
		font-size: 16px;
		line-height: 1.2;
		font-weight: 700;
		margin-top: 10px;
		color: #828282;
	}
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}
