.catalog-items__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 20px;
	@media (max-width: $bp2) {
		grid-template-columns: repeat(3, 1fr);
	}
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp6) {
		grid-template-columns: repeat(1, 1fr);
	}
}
.catalog-item {
	display: flex;
	border: 1px solid $gray-3;
	position: relative;
}
.catalog-item__image {
	flex: 0 0 125px;
	margin-right: $gap-sm;
}
.catalog-item__info {
	padding: $gap-md;
}
.catalog-item__title {
	font-weight: 700;
	margin-bottom: $gap-sm;
	position: relative;
	text-transform: uppercase;
	line-height: 1.2;
	&:before {
		content: "";
		display: block;
		position: absolute;
		left: 0 - $gap-md - $gap-sm;
		top: 5px;
		width: 16px;
		height: 4px;
		background-color: $secondary-color;
	}
}
.catalog-item__text {
	font-size: 13px;
	line-height: 1.3;
	color: $gray-1;
}
.catalog-item__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
