.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $overlay-color;
	// .popup__body
	&__body {
		height: 100%;
		padding: 32px;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		@media (max-width: 425px) {
			padding: 16px;
		}
	}
	// .popup__content
	&__content {
		background-color: #fff;
		padding: 24px 32px 48px;
		position: relative;
		width: 800px;
		max-height: 100%;
		overflow: auto;
		@media (max-width: 1024px) {
			border-radius: 8px;
		}
		@media (max-width: 425px) {
			width: 100%;
			padding: 16px 8px 24px;
		}
	}
	// .popup__close
	&__close {
		position: absolute;
		top: 24px;
		right: 34px;
		line-height: 1;
		font-size: 24px;
		width: 32px;
		height: 32px;
		display: flex;
		justify-content: center;
		align-items: center;
		color: $gray-2;
		transform: translate(12px, -16px);
		@media (max-width: 425px) {
			transform: translate(34px, -16px);
		}
	}
	// .popup__title
	&__title {
		padding: 0 48px 0 0;
		@media (max-width: 425px) {
			padding: 0 24px 0 0;
		}
	}
}
