.news-block {
  margin-bottom: 60px;
  @media (max-width: $bp4) {
		margin-bottom: 30px;
	}
}
.news-block__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  @media (max-width: $bp3) {
		grid-template-columns: 1fr;
	}

}
.news-item {
  display: flex;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
  flex: 0 0 50%;
  @media (max-width: $bp4) {
    flex-direction: column;
  }
  &__image {
    margin-right: 20px;
    position: relative;
    flex: 0 0 40%;
    @media (max-width: $bp4) {
      margin-right: 0;
    }
    &:before {
      display: block;
      content: '';
      padding-top: 80%;
      @media (max-width: $bp3) {
        padding-top: 60%;
      }
    }
    img {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info {
    padding: 15px;
    padding-left: 0;
    @media (max-width: $bp4) {
      padding: 20px;
    }
  }
  &__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.3;
  }
  &__text {
    font-size: 14px;
    line-height: 1.5;
    margin-top: 10px;
  }
}
