.icons-block {
	.title {
		text-align: center;
	}
}
.icons-block__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-lg;
	@media (max-width: $bp3) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp5) {
		grid-template-columns: 1fr;
	}
}
.banner-icons {
	text-align: center;
	padding: $gap-md;
	> * {
		margin-bottom: $gap-md;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.banner-icons__image {
	height: 66px;
	img {
		max-height: 100%;
	}
}
.banner-icons__title {
	font-weight: 700;
	text-transform: uppercase;
	line-height: 1.3;
}
.banner-icons__text {
	font-size: 13px;
	line-height: 1.5;
	color: $gray-1;
}