.artnumber {
	font-size: 14px;
	&_product {
		position: relative;
		top: -20px;
	}
	&_device {
		margin-top: -10px;
	}
}

.devices-block {
	margin-bottom: 60px;
	@media (max-width: $bp4) {
		margin-bottom: 30px;
	}
}
.devices-block__wrap {
	// display: flex;
	// margin: 0 -20px;
	display: flex;
	align-items: center;
	.device {
		width: 100%;
	}
}
.slick-arrow {
	flex: 0 0 50px;
	height: 50px;
	font-size: 0;
	background-color: transparent;
}
.slick-next {
	background-image: url(../img/icon-arrow-01.png);
}
.slick-prev {
	background-image: url(../img/icon-arrow-02.png);
}
.slick-prev {
	margin-right: 10px;
}
.slick-next {
	margin-left: 10px;
}
.devices-block__column {
	padding: 0 10px;
	min-width: 0px;
	display: flex;
}

.device {
	position: relative;
	display: flex;
	flex-direction: column;
	overflow: hidden;

	> * {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	form {
		margin-top: auto;
	}
}
.device__image {}
.device__tags {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	flex-direction: column;
	padding-right: $gap-lg;
	align-items: flex-start;
	max-width: 100%;
}
.device__tag {
	margin-bottom: 5px;
	&:last-child {
		margin-bottom: 0;
	}
}
.device__name {
	font-size: 14px;
	line-height: 1.3;
	@media (max-width: $bp2) {
		font-size: 16px;
	}
}
.zoom {
	position: absolute;
	width: 25px;
	height: 25px;
	top: 5px;
	right: 5px;
	z-index: 2;
}
.device__info {
	margin-top: 20px;
}
.device__price {
	> div {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.price__tag {
	font-size: 14px;
	color: #fff;
	padding: 4px 6px;
	background-color: $secondary-color;
	text-decoration: none;
	margin-right: 10px;
	border-radius: 2px;
}
.price__item {
	&_new {
		font-size: 20px;
		font-weight: 700;
		@media (max-width: $bp2) {
			font-size: 18px;
		}
	}
	&_old {
		display: flex;
		align-items: center;
		font-size: 14px;
		color: #828282;
		span {
			text-decoration: line-through;
		}
	}
}
.order {
	form {
		display: flex;
		> * {
			margin-right: 5px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.button_device {
	background-color: #f2c94c;
	color: $tc1;
	&:hover {
		background-color: #ffb507;
	}
}
.input_qty {
	font-size: 16px;
	padding: 6px 10px;
	border-radius: 4px;
}
.device-order {
	margin-top: auto;
}
.add {
	display: flex;
	> * {
		margin-right: $gap-xs;
		&:last-child {
			margin-right: 0;
		}
	}
}
.add__item {
	align-items: center;
	font-size: 12px;
	.icon {
		height: 12px;
		margin-right: 0;
	}
}










.device_row {
	flex-direction: row;
	text-align: left;
	border-bottom: 1px solid #e0e0e0;
	&:last-child {
		border: none;
	}
	> * {
		margin-bottom: 0;
	}
	.device__image {
		width: 160px;
		margin: 0;
	}
	.price {
		white-space: nowrap;
	}
	.order {
		justify-content: flex-end;
	}
	.add__item {
		margin-bottom: $gap-sm;
		margin-right: 0;
	}
}
.device__column {
	margin-right: 30px;
	&:first-child {
		position: relative;
	}
	&:last-child {
		margin-right: 0;
	}
	&:nth-child(2) {
		flex: 1 1 auto;
	}
	> * {
		margin-bottom: $gap-md;
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.device__info {
	.params {
		tr {
			display: block;
			border: none;
		}
		td {
			font-size: 14px;
			display: inline-block;
			padding: 2px;
			font-weight: 400;
			color: #828282;
			@media (max-width: $bp2) {
				font-size: 14px;
			}
		}
	}
}













//============= slick =================

.device {
	min-width: 0;
}

.device-nav {
	padding-top: $gap-sm;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 8px;
			font-size: 0;
			button {
				font-size: 0;
				width: 11px;
				height: 11px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}

}
