.gifts-block {
	background-color: #E6D0B0; //var
}
.gifts-block__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: $gap-lg;
	@media (max-width: $bp3) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp5) {
		grid-template-columns: 1fr;
	}
}
.banner-gift {
	padding: $gap-md;
	padding-right: 40%;
	position: relative;
	background-color: #fff;
}
.banner-gift__image {
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
	width: 40%;
	padding: 16px;
	padding-left: 4px;
	display: flex;
	img {
		margin: auto;
		max-width: 100%;
		max-height: 100%;
		object-fit: contain;
	}
}
.banner-gift__title {
	font-size: 14px;
	font-weight: 700;
	margin-bottom: $gap-sm;
}
.banner-gift__text {
	font-size: 12px;
	line-height: 1.3;
	color: $gray-1;
}