//filter
$filter-background: #F2F2F2;
$filter-action-background: $secondary-color;




.filter_section {
	margin-bottom: $gap-lg;
}
.filter__wrap {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-gap: $gap-md;
	margin: $gap-md 0;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
}
.filter__item {
	padding: $gap-xs;
	padding-top: 0;
	background-color: #e0e0e0;
	display: flex;
	flex-direction: column;
}
.filter__title {
	font-size: 14px;
	font-weight: 700;
	padding: 10px 0;
	line-height: 1;
}
.filter__values {
	background-color: #fff;
	padding:$gap-sm;
	overflow: hidden;
	overflow-y: auto;
	min-height: 120px;
	flex: 1;
}
.filter__value {
	font-size: 14px;
	margin-bottom: $gap-sm;
	&:last-child {
		margin-bottom: 0;
	}
	label {
		display: flex;
	}
}
.filter__actions {
	grid-column: 1/-1;
	button {
		margin-right: $gap-xs;
		&:last-child {
			margin-right: 0;
		}
	}
	@media(max-width: $bp4) {
		display: flex;
		button {
			flex: 1 0 50%;
		}
	}
}
.filter__button {
	padding-left: 12px;
	img {
		margin-right: 5px;
	}
}
