.contacts-block {
	margin-bottom: 60px;
	@media (max-width: $bp4) {
		margin-bottom: 30px;
	}
	.map {
		@media (max-width: $bp4) {
			height: 240px;
		}
	}
}
.contacts-block__wrap {
	display: grid;
	grid-template-columns: 1fr 390px;
	grid-gap: 40px;
	@media (max-width: $bp3) {
		grid-template-columns: 1fr 1fr;
	}
	@media (max-width: $bp4) {
		grid-template-columns: 1fr;
	}
}
.contacts-block__text {
	font-size: 16px;
	line-height: 1.5;
	@media (max-width: $bp2) {
		font-size: 18px;
	}
}
.contacts-block__item {
	margin-bottom: 20px;
	&:last-child {
		margin-bottom: 0;
	}
	a {
		display: block;
		font-size: 16px;
	}
}
