.blockquote-block {
	font-family: $ff-2;
}
.blockquote-block__wrap {
	text-align: center;
	width: 80%;
	margin-left: auto;
	margin-right: auto;
}
.blockquote-block__title {
	font-family: $ff-2;
	font-size: 36px;
