.compared-container {
  width: 100%;
  overflow-x: auto;
}
.compared {
  min-width: unset;
  tr {
    border: none;
  }
  td,th {
    width: 200px;
    padding: 0;
    padding-right: 15px;
    &:last-child {
      padding-right: 0;
      font-weight: unset;
    }
  }
  th {
    font-weight: unset;
    padding-bottom: 15px;
  }
  .device {
    width: 200px;
    &__name {
      height: 56px;
      overflow: hidden;
    }

  }
  .compared__section {
    font-weight: 700 !important;
    font-size: 15px;
    padding-top: 15px;
  }
  .compared__name {
    font-size: 13px;
    color: #9e9e9e;
    padding-bottom: 0;
    padding-top: 10px;
  }
  .compared__value {
    font-size: 13px;
    color: #333;
    padding-top: 0;
  }
}
