.top-info {
	font-size: 16px;
	padding: 10px 0;
	background-color: #f2f2f2;
	@media (max-width: $bp3) {
		display: none;
	}
}
.top-info__wrap {
	display: flex;
	align-items: center;
	> p {
		&:last-child {
			margin-left: auto;
			text-align: right;
		}
	}
	@media (max-width: $bp2) {
		font-size: 16px;
	}
}
.phones {
	display: flex;
	margin-right: 60px;
	&__item {
		display: flex;
		align-items: center;
		margin-right: 40px;
		&:last-child {
			margin-right: 0;
		}
	}
	&__icon {
		height: 25px;
		margin-right: 10px;
		&.phones__icon_mts {
			padding: 4px 0;
		}
	}
	&__number {
		color: $tc1;
		text-decoration: none;
	}
}
