//pagination
$pagination-active-bgr: $secondary-color;
$pagination-fz: 14px;





.pagination {
	display: flex;
	align-items: baseline;
	margin-top: 70px;
}
.pagination__label {
	margin-right: $gap-md;
}
.pagination__wrap {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0 - $gap-sm;
}
.pagination__item {
	font-size: 16px;
	line-height: 1;
	padding: 6px 10px;
	margin: 0 10px 20px 0;
	text-align: center;
	border: 1px solid $primary-color;
	background-color: #fff;
	border-radius: 2px;
	color: $tc1;
	&:last-child {
		margin-right: 0;
	}
}
.pagination__item_active {
	color: #fff;
	background-color: $primary-color;
}
