.side-menu {
	@media(max-width: $bp3) {
		display: none;
	}
	&__title {
		margin-bottom: 30px;
		font-size: 24px;
		font-weight: 700;
	}
	&__item {
		position: relative;
		font-size: 14px;
		line-height: 1.4;
		margin-bottom: 10px;
		&_level_2,
		&_level_3 {
			font-size: 13px;
			padding-left: 20px;
			margin-bottom: 10px;
			& + .side-menu__item_level_1 {
				margin-top: 30px;
			}
			&:before {
				display: block;
				content: '—';
				position: absolute;
				top: 0;
				left: 0;
			}
		}
		&_level_3 {
			margin-left: 20px;
		}
	}
	&__link {
		color: $tc1;
		&:hover {
			text-decoration: underline;
		}
		&_active {
			font-weight: 700;
			color: $primary-color;
		}
	}
}
