// labels
$label-color: $primary-color;
$label-background: #fff;
$label-active-color: #fff;
$label-active-background: $primary-color;

$label-padding: 6px 15px;






.labels {
	font-size: 0;
}
.labels_section {
	margin-bottom: $gap-lg;
}
.label {
	font-size: 13px;
	display: inline-block;
	padding: $label-padding;
	color: $label-color;
	background-color: $label-background;
	border: 1px solid $label-color;
}
.labels__item {
	margin-bottom: $gap-xs;
	margin-right: $gap-xs;
	&:last-child {
		margin-right: 0;
	}
}
.labels__item_active {
	color: $label-active-color;
	background-color: $label-active-background;
}
