.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	.footer {
		margin-top: auto;
	}
}
.footer {
	padding: 20px 0;
	background-color: $primary-color;
}
.footer__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 24px;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
}
.footer__column {
	font-size: 16px;
	line-height: 1.5;
	color: #fff;
	> * {
		margin-bottom: 20px;
		&:last-child {
			margin: 0;
		}
	}
	@media (max-width: $bp2) {
		font-size: 16px;
	}
	@media (max-width: $bp3) {
		font-size: 14px;
	}
}
.footer__title {
	font-weight: 700;
}
.forever {
	font-size: 14px;
	line-height: 1.3;
	color: #fff;
}
