.header {
	&__wrap {
		display: flex;
		height: 50px;
		margin: 20px 0;
		> * {
			margin-right: 60px;
			&:last-child {
				margin-right: 0;
			}
			@media (max-width: $bp2) {
				margin-right: 30px;
			}
			@media (max-width: $bp6) {
				margin-right: 20px;
			}
		}
		@media (max-width: $bp3) {
			height: unset;
			flex-wrap: wrap;
		}
	}
	&__logo {
		@media (max-width: $bp3) {
			flex: 0 0 160px;
		}
	}
	&__mobile-icons {
		margin-left: auto;
		margin-right: 0;
	}
	&__search {
		display: flex;
		flex: 1;
		form {
			display: flex;
			flex:1;
			padding: 5px 0;
			input {
				flex: 1;
				border: 1px solid #828282;
				border-radius: 2px;
				margin-right: 5px;
				padding: 0 10px;
				font-size: 16px;
			}
			button {
				font-size: 20px;
			}
		}
		@media (max-width: $bp3) {
			order: 1;
			flex: 0 0 100%;
			margin-top: 20px;
		}
	}
	&__icons {
		margin-right: 0;
	}
}
.header-icons {
	display: flex;
	justify-content: space-between;
	> div {
		margin-right: 30px;
		@media (max-width: $bp2) {
			margin-right: 15px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	@media (max-width: $bp3) {
		display: none;
	}
}
.header-icon {
	display: flex;
	align-items: center;
	position: relative;
	&__link {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}
	&__icon {
		position: relative;
	}
	&__label {
		color: #fff;
		background-color: $secondary-color;
		border-radius: 100px;
		font-size: 12px;
		padding: 3px 6px;
		position: absolute;
		top: -8px;
		left: calc(50% + 10px);
		transform: translateX(-50%);
	}
	&__text {
		font-size: 16px;
		margin-left: 10px;
	}
	&:hover {
		.dropdown-menu {
			display: block;
		}
	}
}
.dropdown-menu {
	display: none;
	position: absolute;
	right: 0;
	top: 100%;
	transform: translate(10px, -5px);
	background-color: #fff;
	border-radius: 4px;
	box-shadow: 0 0 15px gray;
	z-index: 2;
	&:before {
		display: block;
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		border: 6px solid transparent;
		border-bottom-color: #fff;
		transform: translate(-15px, -100%);
	}
	&__container {
		padding: 20px;
	}&__link {
		display: block;
		margin-bottom: 10px;
		font-size: 14px;
		white-space: nowrap;
		&:last-child {
			margin-bottom: 0;
		}
	}
}

.header-mobile-icons {
	display: flex;
	> * {
		margin-right: 30px;
		@media (max-width: $bp5) {
			margin-right: 15px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	@media (min-width: $bp2) {
		display: none;
	}
}
.header-mobile-icon {
	position: relative;
	&__label {
		@extend .header-icon__label;
	}
	&__link {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
}
