.brands-block {
  margin-bottom: 60px;
  @media (max-width: $bp4) {
		margin-bottom: 30px;
	}
}
.brands-block__wrap {
  display: flex;
  margin: 0 -8px;
}
.brands-block__item {
  padding: 0 8px;
}
.brand {
  height: 60px;
  padding: 10px;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
  img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
}
.brands-nav {
	padding-top: 15px;
	.slick-dots {
		display: flex;
		justify-content: center;
		align-items: center;
		li {
			margin-right: 7.5px;
			font-size: 0;
			button {
				font-size: 0;
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: $gray-2;
			}
			&:last-child {
				margin-right: 0;
			}
		}
		.slick-active {
			button {
				background-color: $primary-color;
			}
		}
	}
}
