.cart-list__item {
	margin-bottom: $gap-lg;
}
.cart-item {
	display: grid;
	grid-template-areas:
	   "im in pr qt sm dl"
	   "ex ex ex ex ex ex";
	grid-template-columns: 150px 1fr 100px 100px 100px 20px;
	border: 1px solid $cart-item-border-color;
	position: relative;
	> * {
		padding: $cart-item-p;
	}
	.text-label {
		display: block;
		margin-bottom: 4px;
	}
	@media (max-width: $bp4) {
		grid-template-areas:
		   "im in in"
		   "pr qt sm"
		   "ex ex ex";
		grid-template-columns: 100px 1fr 1fr;
	}
}
.cart-item__image {
	grid-area: im;
	padding: 0;
	img {
		object-fit: contain;
	}
}
.cart-item__info {
	grid-area: in;
	display: flex;
	flex-direction: column;
	@media (max-width: $bp4) {
		padding-right: 24px;
	}

}
.cart-item__name {
	margin-bottom: $gap-sm;
	display: inline-block;
	font-size: 16px;
	line-height: 1.3;
}
.cart-item__artnumber {
	display: inline-block;
	color: $gray-1;
	font-size: 14px;
}
.cart-item__price {
	grid-area: pr;
}
.cart-item__quantity {
	grid-area: qt;
}
.cart-item__sum {
	grid-area: sm;
}
.cart-item__delete {
	grid-area: dl;
	padding: 4px 0;
	a {
		color: $gray-2;
		font-weight: 700;
		font-size: 24px;
	}
	@media (max-width: $bp4) {
		grid-area: unset;
		position: absolute;
		top: 0;
		right: 6px;
	}
}


.text-label {
	font-size: 11px;
	color: $gray-1;
}

.cart-total {
	display: flex;
	align-items: center;
	@media (max-width: $bp4) {
		flex-wrap: wrap;
	}
}
.cart-total__text {
	font-size: 24px;
	margin-right: $gap-md;
	@media (max-width: $bp4) {
		margin: 0 0 $gap-sm;
	}
}
.cart-total__buttons {
	font-size: 0;
	button {
		&:last-child {
			margin-left: 10px;
		}
	}
	@media (max-width: $bp4) {
		display: flex;
		width: 100%;
	}
}
.cart-total__button {
	margin-right: $gap-xs;
	font-size: $button-fz-lg;
	&:last-child {
		margin-right: 0;
	}
	@media (max-width: $bp4) {
		flex: 1 1 auto;
		margin-right: $gap-xs;
		&:last-child {
			margin: 0;
		}
	}
}
.cart-confirm__text {
	p {
		margin-bottom: $gap-sm;
	}
}
