.numbers-block__wrap {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: $gap-lg;
	justify-content: center;
	@media (max-width: $bp4) {
		grid-template-columns: 240px;
		grid-row-gap: $gap-xxl; 
	}
}
.banner-numbers {
	text-align: center;
}
.banner-numbers__title {
	font-size: 44px;
	font-weight: 700;
	color: #31869B; //var
}
.banner-numbers__text {
	font-size: 18px;
	font-weight: 700;
}