body {
	font-family: $ff;
	font-size: $fz;
	color: $tc1;
}
a {
	color: $link-color;
}
//======== table =======================================================================
table {
	font-size: 13px;
	color: $gray-1;
	letter-spacing: 0.3px;
	line-height: 1.3;
	tr {
		border-bottom: 1px solid $gray-3;
		&:hover {
			border-color: $gray-2;
		}
	}
	td, th {
		padding: 8px 4px;
		vertical-align: top;
	}
}

.icon {
	display: flex;
	height: 16px;
	width: 24px;
	display: flex;
	margin-right: 4px;
	img {
		max-height: 100%;
		margin: auto;
	}
}
.icon_size_sm {
	width: 16px;
	height: 16px;
}
.icon_size_lg {
	width: 32px;
	height: 32px;
}

.icon-link {
	display: inline-flex;
	align-items: center;
	font-size: 14px;
}
.icon-link_after {
	.icon {
		margin: 0 0 0 4px;
	}
}

.ptb {
	padding-top: $gap-xxl;
	padding-bottom: $gap-xxl;
}
.pt {
	padding-top: $gap-xxl;
}
.pb {
	padding-bottom: $gap-xxl;
}


.divider {
	height: 4px;
	width: 100px;
	background-color: #E6D0B0; // var
	margin: 0 auto;
}

.img {
	position: relative;
	&:before {
		content: "";
		display: block;
		padding-top: 100%;
	}
	img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}

.card {
	border: 1px solid $gray-3;
	padding: 16px;
	background-color: #fff;
}

.device-carousel-colors {
	.card {
		padding: 0;
		border: none;
		text-align: left;
		width: 100%;
		.device__image {
			margin: 0;
			margin-bottom: 8px;
		}
	}
}


.tag {
	padding: 4px 6px;
	font-size: 14px;
	color: #fff;
	background-color: #f2994a;
	border-radius: 4px;
}


//======== container =======================================================================
.container {
	margin: 0 auto;
	@media (min-width: $bp1) {
		width: $cw1;
	}
	@media (max-width: $bp1 - 1) {
		width: $cw2;
		padding: 0 16px;
	}
}
//======== container end =======================================================================
img {
	max-width: 100%;
	max-height: 100%;
}


.text p{
	line-height: 1.3;
	color: #333;
}




.content {
	padding: $gap-xl 0;
	@media(max-width: $bp4) {
		padding-top: 0;
	}
}
.content__wrap {
	display: flex;
	@media(max-width: $bp3) {
		flex-direction: column;
	}
}
.content__side {
	flex: 0 0 240px;
	padding-right: 40px;
	@media (max-width: $bp3) {
		display: none;
	}
	> * {
		margin-bottom: $gap-xl;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.content__main {
	flex: 1 1 auto;
	overflow: hidden;
}



//======================= side info
.side-info {
	font-size: 14px;
	p {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.title {
		font-size: 20px;
	}
}
.side-button {
	button {
		font-size: 15px;
	}
}
//======================= side info end


//======================= side links
.side-links__link {
	display: block;
	margin-bottom: $gap-md;
	font-size: 14px;
	line-height: 1.2;
	&:last-child {
		margin-bottom: 0;
	}
}
//======================= side links end



.text {
	margin-bottom: $gap-lg;
	> * {
		margin-bottom: $gap-md;
	}
}
.date {
	font-size: 13px;
	color: $gray-1;
}
.date_title {
	position: relative;
	top: -$gap-md;
}




.compare-items {
	max-width: 100%;
	overflow: hidden;
	overflow-x: auto;
	display: flex;
	// margin: 0 -$gap-sm;
	padding-bottom: $gap-md;
}
.compare-items__item {
	flex: 0 0 25%;
	// padding: 0 $gap-sm;
	padding-right: 2%;
	overflow: hidden;
}
.compare-items__params {
	margin-top: $gap-md;
	li {
		font-size: 13px;
		display: flex;
		flex-direction: column;
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
		span {
			&:first-child {
				color: $gray-2;
			}
		}
	}
}





// orders history
.orders__item {
	display: grid;
	grid-template-columns: 1fr auto auto;
	grid-gap: $gap-md;
	margin-bottom: $gap-md;
	padding-bottom: $gap-md;
	font-size: 14px;
	border-bottom: 1px solid $gray-3;
	&:last-child {
		margin-bottom: 0;
		border: none;
	}
	 @media (max-width: $bp6) {
		grid-template-rows: auto auto;
		grid-template-columns: 1fr auto;
	}
}
.orders__item {
	overflow: hidden;
}
.orders__item_active {
	padding:$gap-md;
	border: 2px solid $primary-color !important;
}
.past-order__title {
	font-weight: 700;
	margin-bottom: $gap-xs;
}
.past-order__date {
	font-size: 12px;
	color: $gray-2;
}
.past-order__price {
	width: 120px;
}
.past-order__sum {
	margin-bottom: $gap-xs;
}
.past-order__qty {
	font-size: 12px;
	color: $gray-2;
}
.past-order__action {
	 @media (max-width: $bp6) {
		grid-area: 2/1/-1/-1;
	}
}
.past-order__product {
	grid-column: 1/-1;
	// display: none;
	display: grid;
	grid-template-columns: 100px 1fr auto;
	grid-gap: $gap-md;
	border-bottom: 1px solid $gray-3;
	padding-bottom: $gap-md;
	&:last-child {
		border: none;
	}
	@media (max-width: $bp4) {
		grid-template: auto auto /60px 1fr;

	}
}
.past-product__info {
	display: flex;
	flex-direction: column;
	> * {
		margin-bottom: $gap-sm;
	}
}
.past-product__artnumber {
	font-size: 12px;
	color: $gray-2;
}
.past-product__old-price {
	font-size: 12px;
	color: $gray-1;
}
.past-product__action {
	display: grid;
	grid-template-columns: auto auto;
	grid-column-gap: $gap-sm;
	grid-row-gap: $gap-md;
	align-content: start;
	@media (max-width: $bp4) {
		grid-column: 1/-1;
		display: flex;
		width: 100%;
	}
}
.past-product__price {
	grid-column: 1/-1;
	white-space: nowrap;
	@media (max-width: $bp4) {
		flex: 1 1 auto;
		align-self: center;
		font-weight: 700;
	}
}
// orders history end




// info-section
.info-items_section {
	margin-bottom: $gap-lg;
}
.info-items__wrap {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: $gap-md;
	@media (max-width: $bp4) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp5) {
		grid-template-columns: repeat(1, 1fr);
	}
}
.info-item {
	position: relative;
	overflow: hidden;
}
.info-item__image {
	margin: -16px -16px $gap-md;
	&.img:before {
		padding-top: 60%;
	}
}
.info-item__info {
		display: flex;
		flex-direction: column;
	> * {
		margin-bottom: $gap-sm;
		&:last-child {
			margin-bottom: 0;
		}
	}
}
.info-item__date {
	font-size: 12px;
	color: $gray-2;
}
.info-item__text {
	font-size: 13px;
	color: $gray-1;
	line-height: 1.3;
}
.info-item__link {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
// info-section end



.section-text {
	margin-bottom: $gap-md;
	line-height: 1.3;
}



.alert {
	font-size: 14px;
	border-radius: 4px;
	padding: $gap-sm $gap-md;
	margin-bottom: $gap-lg;
	display: flex;
}
.alert_danger {
	background-color: #fff1c8;
	color: #8e7425;
}
.alert_success {
	background-color: #DCEDC8;
    color: #486a20;
}
.alert__title {
	margin-right: $gap-sm;
}

.menu-content {
	padding-top: 10px;
	&__item {
		display: flex;
		padding: 5px 15px;
	}
	&__icon {
		margin-right: 10px;
		width: 24px;
	}
	&__links {
		display: flex;
		flex-direction: column;
		padding-top: 7px;
	}
	&__link {
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 10px;
		}
	}
	&__line {
		height: 1px;
		background-color: #e0e0e0;
		margin-bottom: 5px;
	}
}
