.sections-block {
	margin-bottom: 60px;
	@media (max-width: $bp4) {
		margin-bottom: 30px;
	}
	.catalog-section {
		@media (max-width: $bp5) {
			padding: 15px;
		}
	}
	.catalog-section__title {
		@media (max-width: $bp4) {
			font-size: 16px;
		}
		@media (max-width: $bp5) {
			font-size: 15px;
		}
	}
}
.sections-block__wrap {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 20px;
	@media (max-width: $bp3) {
		grid-template-columns: repeat(2, 1fr);
	}
	@media (max-width: $bp6) {
		grid-template-columns: repeat(1, 1fr);
	}

}
.catalog-section {
	display: flex;
	flex-direction: column;
	padding: 20px;
	border: 1px solid #e0e0e0;
	border-radius: 8px;
	overflow: hidden;


	&__image {
		position: relative;
		&:before {
			content: '';
			display: block;
			padding-top: 80%;
		}
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__subtitle {
		font-size: 12px;
		color: #828282;
		margin-bottom: 5px;
	}
	&__title {
		font-size: 18px;
		font-weight: 700;
		color: $tc1;
	}
	&__text {
		font-size: 16px;
		margin-top: 10px;
		line-height: 1.3;
	}
}
