//view
$view-fz: 14px;
$veiw-border-color: $gray-1;



.view {
	font-size: 16px;
	display: flex;
	align-items: center;
	margin-bottom: $gap-md;
	border-bottom: 2px solid $veiw-border-color;
	padding-bottom: $gap-sm;
	@media (max-width: $bp4) {
		display: none;
	}
}
.view__column {
	margin-right: 40px;
	&:first-child {
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		> a {
			margin-right: 10px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	&:last-child {
		margin-left: auto;
		margin-right: 0;
	}
	span {
		margin-right: 10px;
	}
}
