.location-list {
	display: none;
	position: absolute;
	padding: 10px;
	background-color: white;
	box-shadow: 0 5px 15px rgba(0,0,0,0.2);
	top: 30px;
	li {
		padding: 5px;
	}
	a {
		font-size: 14px;
		color: $link-color;
	}
	&:before {
		content: '';
		display: block;
		border: 6px solid transparent;
		border-bottom-color: #fff;
		position: absolute;
		top: -12px;
		left: 10px;
	}
}
