.main-menu {
	margin-bottom: 20px;
	@media (max-width: $bp3) {
		display: none;
	}
	&__icon {
		margin-right: 10px;
	}
}
.main-menu__wrap {
	display: flex;
	align-items: center;
	background-color: $primary-color;
	border-radius: 4px;
}
.main-menu__item {
	height: 54px;
	flex: 1 1 auto;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 20px;
	display: flex;
	position: relative;
	&:hover, &:first-child {
		background-color: #074B96;
	}
	&:first-child {
		border-radius: 4px 0 0 4px;
	}
	&:last-child {
		border-radius: 0 4px 4px 0;
	}
	> a {
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
	}
	@media (max-width: $bp2) {
		font-size: 20px;
	}
}



// popup menu
.popup-catalog-menu {
	position: absolute;
	background-color: #f2f2f2;
	z-index: 2;
	top: 54px;
	left: 0;
	display: flex;
	box-shadow: 0 10px 15px rgba(0,0,0,.2);
	a {
		padding: 8px 15px;
		font-weight: 400;
		font-size: 14px;
		text-transform: none;
		line-height: 1.3;
		&:hover {
			background-color: #fff;
		}
	}
}
.sections {
	width: 250px;
	display: flex;
	flex-direction: column;
	padding: $gap-sm 0;
}
.subsections {
	display: flex;
	flex-direction: column;
	width: 250px;
	background-color: #fff;
	padding: $gap-sm 0;
}
.popup-text-menu {
	@extend .popup-catalog-menu;
}
// popup menu end
