.path_common {
	margin-bottom: $gap-sm;
}
.path__item {
	display: inline-flex;
	align-items: center;
}
.path__link {
	font-size: 13px;
	line-height: 1;
	color: $gray-1; //var $path-color: $gray-1 тогда не надо будет лазить по файлам
}
.path__arrow {
	border: 4px solid transparent;
	border-left-color: $gray-1;
	font-size: 0;
	margin: 0 $gap-xs;
}